<template>
  <div class="success-case full-screen">
    <div class="page-panel">
      <pem-scroll-view 
        class="scroll-view"
        ref="scroll" 
        :pullup=true
        @pullUpLoad="listMore">
        <div class="container">
          <div class="title">{{ result.title }}</div>
          <div class="bar">
            <span v-if="result.from">来源: {{ result.from }}</span>
            <span class="time">{{ result.date }}</span>
          </div>
          <div class="content" v-html="result.content"></div>
        </div>
      </pem-scroll-view>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
import PemScrollView from 'components/scroll-view/scroll-view'
import { MiniSiteApi } from 'api'
import { setTitle } from 'common/js/util'

export default {
  data () {
    return {
      isMore: false,
      result: {}
    }
  },
  components: {
    PemScrollView
  },
  activated () {
    let options = {id: this.$route.query.id}
    this._getIndexCaseDetail(options)
  },
  created () {
    this._initWXShare()
    this._setTitle('成功案例')
  },
  methods: {
    _initWXShare () {
      let title = this.result.title || '我正在使用项目工场，有料的BP、有趣的VC都在这儿，一起来看项目吧！'
      this.wxShare({ title })
    },
    _setTitle (title) {
      setTitle(title)
    },
    _getIndexCaseDetail (options) {
      MiniSiteApi.getIndexCaseDetail(options).then(res => {
        this.result = res || {}
        this._initWXShare()
        this.$nextTick(function () {
          this.$refs.scroll.scrollTo(0, 0, 0)
          this.refresh()
        })
        if (this.result && (this.result.content || '').indexOf('<img') > -1) {
          this.isMore = true
        }
      }).catch((error) => {
        console.error(error)
      })
    },
    backtotop () {
      this.$refs.scroll.scrollTo(0, 0, 300)
    },
    refresh () {
      this.$refs.scroll.refresh()
    },
    listMore () {
      if (this.isMore) {
        this.refresh()
        this.isMore = false
      }
    }
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"
  @import "~common/stylus/mixin"
  .page-panel
    top: 0
  .success-case >>> p
    padding-bottom: 10px
  .container
    padding: 25px 15px 50px
    .title
      font-size: 20px
      font-weight: bold
      color: #41403E
      line-height: 26px
      padding-bottom: 10px
    .bar
      font-size: 12px
      color: #A7A5A3
      .time
        display: inline-block
        padding-left: 10px
    .content
      padding-top: 20px
      font-size: 16px
      line-height: 26px
      color: #585755
</style>



